<template>
  <div>

    <!-- 顶部菜单 --> 
    <scaffold-bar class="position-fixed"></scaffold-bar>

    <div class="container-fluid bg-hui">
      
      <div class="row wrapper">
        <!-- 左菜单 --> 
        <div class="col-sm-12 col-lg-4">&nbsp;</div>
        <div class="col-sm-12 col-lg-4">
          <div class="login-title mt-5">登录</div>
          <div class="login-panel bg-white def-shadow mt-2 text-center">
            <login-form ref="form"></login-form>
            <button class="btn btn-outline-info login-btn" @click="submit">提交</button>
          </div>
        </div>
        <div class="col-sm-12 col-lg-4"></div>
      </div>
    </div>

    <!-- 提示 -->
    <toast></toast>
  </div>
</template>

<script>
import ScaffoldBar from '@/components/TopBar/ScaffoldBar'
import Toast from '@/components/Dialog/Toast.vue'
import LoginForm from './LoginForm.vue'
export default {
  name: "LoginView",
  components: {
    ScaffoldBar,
    Toast,
    LoginForm
  },
  methods: {
    submit() {
      this.$refs.form.submitLoginForm()
    }
  }
}
</script>

<style scoped lang="sass">
@import "@/common/sass/base.sass"

.login-title
  color: $scaffold
  font-size: 38px

.container-fluid
  padding-left: 0
  padding-right: 0
  z-index: 80

.wrapper
  height: 100vh
  padding-top: $topBarH !important

.login-panel 
  min-height: 60px
  border-radius: $horizon
  padding: $horizon

.login-btn
  padding-left: $horizonXL
  padding-right: $horizonXL
  margin-bottom: $horizonS
</style>


