<template>
  <form class="form form-panel text-left">
    <div class="form-group">
      <!-- label for="email">电邮</label -->
      <input type="text" class="form-control" id="email" placeholder="电邮地址" v-model="email">
      <small id="emailTip" class="form-text text-muted">{{ tip_email }}</small>
    </div>
    <div class="form-group">
      <!-- label for="password">密码</label -->
      <input type="password" class="form-control" id="password" placeholder="密码" v-model="password">
      <small id="passwordTip" class="form-text text-muted">{{ tip_password }}</small>
    </div>
  </form>

</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'

export default {
  name: 'LoginForm',
  data() {
    return {
      password: '',
      email: '',

      tip_email: '',
      tip_password: ''
    }
  },
  methods: {
    submitLoginForm() {
      const validate = this.validateForm()
      console.log('提交')
    },
    // 
    tipClose(block) {
      setTimeout(() => {
        if (block == 'email') { this.tip_email = '' }
        else if (block == 'password') { this.tip_password = '' }
      }, 4200)
    },
    tipForm(res, block) {
      let show = ''
      if (res != true) {
        show = res
      }
      // 
      if (block == 'email') { this.tip_email = show }
      else if (block == 'password') { this.tip_password = show }

      this.tipClose(block)
    },
    validateForm() {
      let res = validate.val_empty(this.email, '电邮')
      if (res == true) {
        res = validate.val_email(this.email, '电邮')
        this.tipForm(res, 'email')
      } else { this.tipForm(res, 'email') }

      res = validate.val_empty(this.password, '密码')
      if (res == true) {
        res = validate.val_char(this.password, '密码')
        this.tipForm(res, 'password')
      } else { this.tipForm(res, 'password') }
    }
  }
}
</script>

<style scoped lang="sass">
@import "@/common/sass/base.sass"

.form-panel
  margin: 0
</style>


