<template>
  <div class="row top-bar p-0">
    <div class="col-sm-8"></div>
    <div class="col-sm-4 text-right top-bar-right-content">
      
      {{ email }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'TopBar',
  data() {
    return {
      email: 'support@manfulls.com'
    }
  }
}
</script>

<style scoped lang="sass">
@import "@/common/sass/base.sass"

.top-bar
  width: 100vw
  z-index: 999
  padding: 0 !important
  margin: 0
  overflow: auto

.top-bar > div
  background: $scaffold
  color: white
  height: $topBarH

.top-bar-right-content
  padding-right: $topBarRightPadding
  display: flex
  align-items: center
  justify-content: flex-end
  vertical-align: middle
  height: $topBarH
  overflow: auto

.tbd-panel
  z-index: 999
</style>


